@import url('https://fonts.googleapis.com/css?family=Montserrat:300,600');
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  min-height: 100vh;
  width: 100%;
  /* include the chosen font and colors */
  font-family: 'Montserrat', sans-serif;
  color: hsla(0, 0%, 15%, 0.95);
  /* for the background, include a linear gradient with the chosen blue colors */
  background: -webkit-gradient(linear,left top, right bottom,from(hsl(198, 76%, 50%)), to(hsl(228, 62%, 36%)));
  background: -webkit-linear-gradient(top left,hsl(198, 76%, 50%), hsl(228, 62%, 36%));
  background: -o-linear-gradient(top left,hsl(198, 76%, 50%), hsl(228, 62%, 36%));
  background: linear-gradient(to bottom right,hsl(198, 76%, 50%), hsl(228, 62%, 36%));
}



