.InputField {
  /* display the contents of the input field (such as label and input) in a single column, vertically aligned to the start of the container */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  /* separate the input from the surrounding content (h1 and submit button) */
  margin: 2rem 0 0.5rem;
}
.InputField label {
  /* include some horizontal padding and a small indicator to the left of the text */
  padding: 0 0.3rem;
  border-left: 2.5px solid hsl(348, 98%, 66%);
}

.InputField input {
  /* include the chosen font */
  font-family: inherit;
  font-size: 1rem;
  /* style the input elements to have a transparent, slightly rounded border */
  padding: 0.8rem 1.2rem;
  border-radius: 2px;
  border: 2px solid hsla(0, 0%, 15%, 0.2);
  margin: 0.5rem 0;
}



