.SignUpForm {
  /* display the contents of the form in a single column layout */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  /* include notable white space around the sign up form content */
  padding: 10vh 15vw;
  /* style the form to be a white card with a touch of bright shadow */
  border-radius: 12px;
  background: hsl(195, 50%, 98%);
  -webkit-box-shadow: 0 1px 5px hsl(195, 50%, 98%);
          box-shadow: 0 1px 5px hsl(195, 50%, 98%);
  /* center the text in the form */
  text-align: center;

  /* by default, hide the component and slightly offset it to the bottom */
  opacity: 0;
  -webkit-transform: translateY(10vh);
      -ms-transform: translateY(10vh);
          transform: translateY(10vh);
  /* transition to ease the change in opacity and translateY  */
  -webkit-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
  /* slightly delay the transition which removes the SignUpForm from view, to assess the click on the submit button */
  -webkit-transition-delay: 0.2s;
       -o-transition-delay: 0.2s;
          transition-delay: 0.2s;

}

/* include a class which is iteratively added and removed to animate the .SignUpForm compoentn */
.AnimateForm {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  /* specifying the transition on the .Animate class as well means that when the class is added, this transition applies (with 0.5s transition-duration)
  when the class is removed, the previous transition takes hold (with 0.2s transition-duration) */
  -webkit-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}

.SignUpForm h1 {
  text-transform: uppercase;
  font-size: calc(2rem + 1vw);
  /* give more transparency the header, which would otherwise be too heavy on the page */
  color: hsla(0, 0%, 15%, 0.7);
  font-weight: 300;
}

.SignUpForm button {
  /* style the button with the chosen accent color  */
  color: hsl(195, 50%, 98%);
  background: #FD5477;
  font-family: inherit;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 600;

  margin: 0.5rem 0;
  padding: 0.8rem 1.2rem;
  border:none;
  border-radius: 10px;
  cursor: pointer;
  /* transition for the hover and active states */
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
.SignUpForm button:hover {
  -webkit-box-shadow: 0 2.5px 7.5px #FD5477, 0 -2.5px 7.5px #FD5477;
          box-shadow: 0 2.5px 7.5px #FD5477, 0 -2.5px 7.5px #FD5477;
}
.SignUpForm button:active {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}



